<template>
    <Card class="tableWrap inputWrap">
                <p slot="title"></p>
        <div class="table-header flex flex-wrap">
            <div class="table-item flex-1"  v-for="(item, index) in columns" :key="index">
                <Poptip v-if="item.key === 'footPier'" trigger="hover" 
                width="200" word-wrap
                title="" content="442-02">
                    {{item.title}}
                </Poptip>
                <span v-else>{{item.title}}</span>
            </div>
        </div>
        <div class="table-row flex flex-wrap" v-for="(item, index) in inputList" :key="index">
            <div class="table-item flex-1" v-for="(ele, eleIndex) in columns" :key="eleIndex">
                    <div v-if="ele.key === 'action'">
                        <Button type="primary" @click.native="addNew(item, index)"></Button>
                    </div>
                    <span v-if="ele.key === 'name'">
                        {{item[ele.key]}}
                    </span>
                    <div v-if="ele.key !== 'action' && ele.key !== 'name'">
                        <div v-if="ele.key === 'doorLeafThick'">
                            <InputNumber v-if="item.name === ''" v-model="item[ele.key]"></InputNumber>
                        </div>
                        <div v-if="ele.key === 'footPier'">
                            <Input v-if="item.name !== ''" v-model="item[ele.key]"></Input>
                        </div>
                        <div v-if="ele.key !== 'doorLeafThick' && ele.key !== 'footPier'">
                            <InputNumber v-if="item.name !== ''" v-model="item[ele.key]"></InputNumber>
                            <InputNumber v-if="item.name === '' && ele.key !== 'innerToTop'" v-model="item[ele.key]"></InputNumber>
                        </div>
                    </div>
            </div>
        </div>
    </Card>
</template>

<script>
export default {
    data() {
        return {
            notInput: ['action'],
            inputList: [{
                name: '',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 
                doorLeafHeight: null,
                // 
                doorLeafWidth: null,
                doorLeafThick: 45,
                outToTop: null,
                innerToTop: null,
                footPier: null,
            }, {
                name: '',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 
                doorLeafHeight: null,
                // 
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
                footPier: null,
            }, {
                name: '',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 
                doorLeafHeight: null,
                // 
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
            }, {
                name: '',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 
                doorLeafHeight: null,
                // 
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
                footPier: null,
            }, {
                name: '',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 
                doorLeafHeight: null,
                // 
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
                footPier: null,
            }],
            columns: [{
                title: '',
                key: 'name'
            }, {
                title: '',
                key: 'doorHeight'
            }, {
                title: '',
                key: 'doorWidth'
            },{
                title: '',
                key: 'doorThick'
            }, {
                title: '',
                key: 'outToTop'
            }, {
                title: '',
                key: 'innerToTop'
            }, {
                title: '',
                key: 'footPier'
            },{
                title: '',
                key: 'action'
            }],
            // 
            nulllMap: {
                'outToTop' : true, 
                'innerToTop': true, 
                'doorLeafHeight': true, 
                'doorLeafWidth': true, 
                'footPier': true
            },
            wordMap: {
                'name': '',
                'doorHeight': '',
                'doorWidth': '',
                'doorThick': '',
            }
        }
    },
    computed: {
    },
    mounted() {
    },
    methods: {
        cloneJson(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        addNew(item, index) {
            
            for(let i in item) {
                if(!this.nulllMap[i] && !item[i]) {
                    this.$Message.info('' + this.wordMap[i] + '!');
                    return
                }
            }
            this.$emit('addNew', this.cloneJson(item))
            // 
            for(let i in this.inputList[index]) {
                if(i !== 'name') {
                    this.inputList[index][i] = null
                }
            }
            if(index === 0) {
                this.inputList[index].doorLeafThick = 45
            }
        }
    },
}
</script>

<style lang="scss">

@import 'src/assets/common.scss';
.inputWrap {
    position: fixed !important;
    right: 10px;
    background: #fff;
    width: 800px;
    z-index: 10;
    top: 120px;
}
</style>
